//URL    API SpaceX graphQL
const URL = 'https://api.spacex.land/graphql'

//     API   
const allShipsSpaceX = () => {
    return `{
      ships(limit: ${100}) {
        id
        name
        home_port
        type
      }
  }`
}

//     API      id
const currentShip = (shipId) => {
    return `{
      ship(id: "${shipId}") {
        name
        weight_kg
        year_built
        type
        home_port
        missions {
          name
        }
      } 
    }`
}

//Request-     API (    graphQL)
const instance = (queryData) => fetch(URL, {
    method: 'POST',
    body: JSON.stringify({
        query: queryData
    }),
    headers: {
        'content-type': 'application/json'
    }
})

export const shipsAPI = {
    //  API   
    async getShips() {
        const response = await instance(allShipsSpaceX())
        const json = await response.json();
        return json.data
    },

    //  API      
    async getCurrentShip(shipId) {
        const response = await instance(currentShip(shipId))
        const json = await response.json();
        return json.data.ship
    },
}


